<template>
  <ve-bmap
    :settings="chartSettings"
    :series="chartSeries"
    :tooltip="chartTooltip"
    :visualMap="visualMap"
    :events="chartEvents"
    style="width:100%;"
    >
  </ve-bmap>
</template>

<script>
  import bus from '../../assets/eventBus.js'
  export default {
    data () {
      this.chartSettings = {
        key: 'PyOXfiEWr28QPesl28HcV4pEUqjH9tvP',
        bmap: {
          center: [106.5, 29.5],
          zoom: 10,
          roam: true,
          mapStyle: {
            styleJson:[
                  {
                    "featureType": "water",
                    "elementType": "all",
                    "stylers": {
                        "color": "#021019"
                  }
          },
          {
            "featureType": "highway",
             "elementType": "geometry.fill",
             "stylers": {
               "color": "#000000"
                    }
          },
          {
            "featureType": "highway",
            "elementType": "geometry.stroke",
             "stylers": {
                  "color": "#147a92"
                    }
          },
          {
             "featureType": "arterial",
             "elementType": "geometry.fill",
             "stylers": {
                   "color": "#000000"
                    }
          },
          {
             "featureType": "arterial",
             "elementType": "geometry.stroke",
              "stylers": {
                 "color": "#0b3d51"
                    }
          },
          {
             "featureType": "local",
             "elementType": "geometry",
             "stylers": {
                  "color": "#000000"
                    }
          },
          {
                    "featureType": "land",
                    "elementType": "all",
                    "stylers": {
                              "color": "#08304b"
                    }
          },
          {
                    "featureType": "railway",
                    "elementType": "geometry.fill",
                    "stylers": {
                              "color": "#000000"
                    }
          },
          {
                    "featureType": "railway",
                    "elementType": "geometry.stroke",
                    "stylers": {
                              "color": "#08304b"
                    }
          },
          {
                    "featureType": "subway",
                    "elementType": "geometry",
                    "stylers": {
                              "lightness": -70
                    }
          },
          {
                    "featureType": "building",
                    "elementType": "geometry.fill",
                    "stylers": {
                              "color": "#000000"
                    }
          },
          {
                    "featureType": "all",
                    "elementType": "labels.text.fill",
                    "stylers": {
                              "color": "#857f7f"
                    }
          },
          {
                    "featureType": "all",
                    "elementType": "labels.text.stroke",
                    "stylers": {
                              "color": "#000000"
                    }
          },
          {
                    "featureType": "building",
                    "elementType": "geometry",
                    "stylers": {
                              "color": "#022338"
                    }
          },
          {
                    "featureType": "green",
                    "elementType": "geometry",
                    "stylers": {
                              "color": "#062032"
                    }
          },
          {
                    "featureType": "boundary",
                    "elementType": "all",
                    "stylers": {
                              "color": "#1e1c1c"
                    }
          },
          {
                    "featureType": "manmade",
                    "elementType": "geometry",
                    "stylers": {
                              "color": "#022338"
                    }
          },
          {
                    "featureType": "poi",
                    "elementType": "all",
                    "stylers": {
                              "visibility": "off"
                    }
          },
          {
                    "featureType": "all",
                    "elementType": "labels.icon",
                    "stylers": {
                              "visibility": "off"
                    }
          },
          {
                    "featureType": "all",
                    "elementType": "labels.text.fill",
                    "stylers": {
                              "color": "#2da0c6",
                              "visibility": "on"
                }
              }
            ]
         }
        }
      }
      this.chartTooltip = {
                trigger: 'item', 
                formatter: function(data) 
                 {
                   let value=data.value
                  return value[3] + '<br/>' +'接纳人数'+ '：'+value[2]; //将小数转化为百分数显示
                 }
             }
      var self = this
      this.chartEvents = {
        click: function (e) {
          bus.$emit('clickMap',e.data[3],e.data[4])
        }
      }
      return {
        chartSeries: [
          {
            type: 'scatter',
            coordinateSystem: 'bmap',                 
            data: [],
            itemStyle: {
                normal: {
                    color: '#ddb926'
                }
            }
            
          }
        ],
         visualMap:[
            {
              min:5000,
              max:30000,
              show:false,
              dimension: 2,
              inRange: {
                 symbolSize: [5, 30]
              },
              outOfRange: {
                 symbolSize: [5, 30],
                 color: ['rgba(255,255,255,.2)']
              },
            },
            
          ],
      }
    },
    mounted(){
      let self=this
      this.getInitData();
       bus.$on('tableClick',function(pid,wid,name){
            self.findMapDataByPid(pid)
             }) 
    },
    methods:{
      getInitData(){
        let that=this
        this.$http.post('/map')
        .then(function(res){
          console.log('成功')
          that.chartSeries[0].data=res.data
        })
        .catch(function(err){
          console.log('错误'+err)
        })
      },
      findMapDataByPid(pid){
        let that=this
        this.$http.post('/findMap',{Id:pid}).then((res)=>{ 
          that.chartSeries[0].data=res.data}
        )
      }
    }
  }
  
</script>


<style >

</style>



