<template>
  <div id="app" >
    <div class="main-left">
      <bmap></bmap>
      <ring></ring>
    </div>
    <div class="main-middle">
      <span class="headline">基于城市网吧数据的违规网吧行为分析</span>
       <div class="block">
         <mySelect></mySelect>
       </div>
       <scatter></scatter>
    </div>
    <div class="main-right">
      <div class="tab">
        <MyTable></MyTable>
      </div>
      <calendar></calendar>
    </div>
  </div>
</template>

<script>
import bmap from './components/bmap/bmap.vue'
import MyTable from './components/right/MyTable.vue'
import calendar from './components/right/calendar.vue'
import ring from './components/middle/ring.vue'
import scatter from './components/middle/scatter.vue'
import mySelect from './components/right/select.vue'
export default {
  name: 'App',
  components: {
    bmap,
    MyTable,
    calendar,
    ring,
    scatter,
    mySelect
  }
}
</script>

<style>
#app {
position: absolute;
margin: 0px;
padding: 0px;
width: 100%;
height: 100%;
display: flex;
background: url('./assets/bg.jpg');
background-origin: border-box;
}
.main-left{
    margin: 8px;
    padding-top: 5px;
    width: 35%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items:center;

}
.main-middle{
  margin: 8px;
  padding: 0px;
  width: 30%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.headline{
  padding-top: 10px;
  font-size:20px;
  width:80%;
  height:10%;
  color:white;
  text-align: center;
}
.main-right{
margin: 8px 4px;
padding: 0px;
width: 35%;
height: 100%;
display: flex;
flex-direction: column;
justify-content: space-between;
align-items:center;
}
.tab{
  display: flex;
    width: 100%;
    height: 60%;
    justify-content: flex-start;
    flex-direction: column;
    flex-wrap: nowrap;
}
.block{
  display: flex;
  height: 24%;
  width: 100%;
  justify-content: flex-start;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items:center;
}
</style>



