<template>
    <div class="s">
      <span style="color:#fff;font-size:13%;text-align:center">年龄</span>
      <el-slider
        v-model="age"
        range
        show-stops
        :max="100"
        v-on:change="change">
      </el-slider>
      <span style="color:#fff;font-size:13%;text-align:center">单次连续上网时间</span>
      <el-slider
        v-model="once"
        show-stops
        :max="200"
        v-on:change='change'>
      </el-slider>
      <span style="color:#fff;font-size:13%;text-align:center">上网总时长</span>
      <el-slider
        v-model="all"
        show-stops
        :max="1500"
        v-on:change='change'>
      </el-slider>
  </div>
</template>
<script>
  import bus from '../../assets/eventBus.js'
  export default {
    
    data() {
      return {
        age: [1, 100],
        once: 0,
        all:0
      }
    },
    methods:{
       change(value){
        console.log('change-'+this.age)
        bus.$emit("changeSelect",this.age,this.once,this.all);
      }
    }
  }
</script>
<style>
.s{
 display: flex;
 width: 80%;
 height: 30%;
 justify-content: flex-start;
  flex-direction: column;
  flex-wrap: nowrap;
}
</style>




