<template>
 <div id="my-table">
    <el-table
    :data="tableData"
    :header-row-style="{height:'20px'}"
    v-on:row-click="tableClick"
     size="mini"
     height='100'
     >
    <el-table-column
      fixed
      prop="identityId"
      label="身份id"
      width="150">
    </el-table-column>
    <el-table-column
      prop="name"
      label="姓名"
      width="60">
    </el-table-column>
    <el-table-column
      prop="sex"
      label="性别"
      width="50">
    </el-table-column>
    <el-table-column
      prop="age"
      label="年龄"
      width="80"
      sortable
      >
    </el-table-column>
    <el-table-column
      prop="onceTime"
      label="单次连续上网时长(h)"
      width="160"
      sortable>
    </el-table-column>
    <el-table-column
      prop="allTime"
      label="上网总时长(h)"
      width="120"
      sortable>
    </el-table-column>
    <el-table-column
      prop="frequency"
      label="上网次数"
      width="100"
      sortable>
    </el-table-column>
  </el-table>
 </div>
</template>

<script>
  import bus from '../../assets/eventBus.js'
  export default {
    name:'MyTable',
    data() {
      return {
         tableData: [],
         wid:'12345',
         name:''
      }
    },
    mounted(){
      let self=this
      this.initTable(this.name,this.wid)
     
      bus.$on("changeSelect",function(age,once,all){
        
        self.changeData(this.wid,age,once,all);
      })
      bus.$on('clickMap',function(name,wid){
          self.name=name
          self.wid=wid
          self.initTable(name,wid)
      })  
    },
    methods:{
      initTable(name,wid){
        let self=this
        self.name=name
        self.wid=wid
        this.$http.post('/tableData',
              {ID: this.wid,}                             
        ).then((res)=>{
          self.tableData=res.data
          console.log(self.tableData)
        })
      },
    changeData(wid,age,once,all){
      
        let self=this
        this.$http.post('/tableChangeData',
              {ID: wid,          
              ageS:age[0],         
              ageE:age[1],         
              onceTime:once,       
              allTime:all}                             
        ).then((res)=>{
          self.tableData=res.data
        })
      },
    tableClick(row, event, column){
     bus.$emit('tableClick',row.identityId,this.wid,row.name)
    }
    }
  }
</script>
<style>
#my-table{
    display: flex;
    width: 100%;
    height: 90%;
    justify-content: flex-start;
    flex-direction: column;
    flex-wrap: nowrap;
   
}
.el-table th{ 
  width: 100%;
  font-size: 20%;
  background-color:#08304a;
  
  border-width: 1px;
  
}
.el-table tr{ 
  width: 100%;
  background-color:#08304a;
  border-style: solid;
  border-width: 1px;
}
#my-table /deep/ .el-table__body-wrapper{
  background-color:#08304a;
}
</style>



