<template>
  <div id="calendar" ></div>
</template>

<script>
    import bus from '../../assets/eventBus.js'
    export default {
        name: 'calendar',
        data(){
            return{
                wid:'12345',
                pid:'12344',
                data:[],
            }
        },
        mounted(){
            this.drawLine(this.wid,this.pid,this.name)
            let self=this
            bus.$on('tableClick',function(pid,wid,name){
            self.drawLine(pid,wid,name)
             }) 
        },
        methods: {
            drawLine(pid,wid,name){
                let self=this
                self.name=name
                this.$http.post('/calendarData',
                    {Pid: pid,
                    Wid:wid}                             
                ).then((res)=>{
                // 基于准备好的dom，初始化echarts实例
                let myChart = this.$echarts.init(document.getElementById('calendar'))
                // 绘制图表
                self.data=res.data
                console.log('calendar'+res.data)
                myChart.setOption({
                         left:'center',
                         title: {
                            top: 0,
                            text: '上网记录',
                            
                            left: 'center',
                            textStyle: {
                                color: '#fff'
                            }
                        },
                        tooltip : {
                                    trigger: 'item', 
                                    formatter:function(val){
                                          return self.name+'<br/>'+'时长：'+val.value[1]
                                    }
                                },
                        legend: {
                            top: '20',
                            left: '30',
                            data:['时长'],
                            textStyle: {
                                color: '#fff'
                            }
                        },
                        calendar: [{
                            top: 80,
                            left: 'center',
                            range: ['2016-10-01', '2016-12-30'],
                            splitLine: {
                                show: true,
                                lineStyle: {
                                    color: '#000',
                                    width: 4,
                                    type: 'solid'
                                }
                            },
                            dayLabel:{
                                nameMap: 'cn',
                                color:'#fff',
                                firstDay:1
                            },
                            monthLabel:{
                                nameMap: 'cn',
                                color:'#fff'
                            },
                            yearLabel: {
                                formatter: '{start} ',
                                textStyle: {
                                    color: '#fff'
                                }
                            },
                            itemStyle: {
                                normal: {
                                    color: '#323c48',
                                    borderWidth: 1,
                                    borderColor: '#000'
                                }
                            }
                        }],
                         visualMap: [
                            {
                            left: 'right',
                            top:'30%',
                            calculable: true,
                            min:1,
                            max:24,
                            range:[1,12],
                            itemWidth: 15,
                            itemHeight: 100,
                            show:true,
                            dimension: 1,
                            inRange: {
                            symbolSize: [5, 20]
                            },
                            outOfRange: {
                                symbolSize: [5,20],
                                color: ['red']
                            },
                            controller:{
                                inRange: {
                                color:['#ddb926']
                                },
                                outOfRange: {
                                color: ['red']
                            },
                            },
                            textStyle: {
                                color:['#fff']
                           }
                           }
                           
                         ],
                        series : [
                            {
                                name: '时长',
                                type: 'scatter',
                                coordinateSystem: 'calendar',
                                data: self.data,
                                itemStyle: {
                                    normal: {
                                        color: '#ddb926'
                                    }
                                }
                            },
                        ]
                });
            })
            }
        }

    }
</script>
<style>
 #calendar{
     display: flex;
     padding: 3%;
     width: 100%;
     height: 40%;
     flex-direction: column;
    justify-content: flex-start;
    align-items:center;
 }

</style>




