const Mock = require('mockjs')

const Random = Mock.Random
// bmap
// 获取网吧信息[经度,纬度,三个月总接纳人数,违规接纳人数、网吧名称，网吧id]
// this.$http.post('/map')
Mock.mock('/map', /post|get/i, function () {
  let data = [
    // value值分别为经度、纬度、三个月总接纳人数、网吧名字、网吧id
    [106.569253, 29.586056, 10000, '江北****会所', 50010510000039],
    [106.488929, 29.602083, 9000, '乌托****网吧', 50010510000040],
    [106.653753, 29.626325, 15000, '晴天****会所', 50010510000051],
    [106.488929, 29.702083, 18000, '天下****网吧', 50010510000040],
    [106.653753, 29.826325, 25000, '风信子****会所', 50010510000051]
  ]
  console.log(data)
  return data
}
)
// 通过身份id查找网吧位置信息
// this.$http.post('/findMap',{Id:pid})
Mock.mock('/findMap', /post|get/i, function (option) {
  console.log(option)
  let data = [
    // value值分别为经度、纬度、三个月总接纳人数、网吧名字、网吧id
    [106.569253, Random.float(29.4, 29.8, 0, 5), 10000, '江北****会所', 50010510000039],
    [106.488929, Random.float(29.4, 29.8, 0, 5), 15000, '重庆****网吧', 50010510000040],
    [106.653753, Random.float(29.4, 29.8, 0, 5), 20000, '爱好****会所', 50010510000051]
  ]
  console.log(data)
  return data
}
)
// ring
// 通过参数（网吧id：wid)获取某网吧违规接纳情况，
// [{value:(人数)，name:(类型),..]
// self.$http.post('/ringData',{ID: wid})
Mock.mock('/ringData', /post|get/i, function (options) {
  console.log(options)
  let data = [
    {value: Random.integer(300, 400), name: '<18'},
    {value: Random.integer(30, 100), name: '18-40'},
    {value: Random.integer(200, 400), name: '40-60'},
    {value: Random.integer(200, 400), name: '其它'}]
  return data
}
)

// scatter
// 通过参数（网吧id：wid)获取某网吧违规情况信息[[时间段，正常人数，未成年人数，可疑人员人数],...]
// this.$http.post('/scatterData',{ID: wid})
Mock.mock('/scatterData', /post|get/i, function (options) {
  console.log(options)
  let value = []
  let i = 0
  for (i; i < 24; i++) {
    // 分别表示时间段，'<18'，'18-40'，'40-60'，'其它'
    let temp = [i + 1, Random.integer(300, 500), Random.integer(20, 100), Random.integer(50, 200), Random.integer(50, 200)]
    value.push(temp)
  }

  // console.log('成功'+data[0].value)
  return value
}
)

// MyTable
// 通过参数（网吧id：wid)获取某网吧上网个人信息
// 返回数据[
//   {
//     identityId: Random.integer(1, 100),
//     name: Random.cname(),
//     sex: Random.integer(0, 1),
//     age: Random.integer(10, 80),
//     onceTime: Random.float(1, 20, 0, 1),
//     allTime: Random.float(20, 1000, 0, 1),
//     frequency: Random.integer(2, 100),
//     mark: Random.integer(1, 3)
//   },...
// ]
//  this.$http.post('/tableData',{ID: this.wid,})
Mock.mock('/tableData', /post|get/i, function (options) {
  console.log(options)
  let value = []
  let i = 0
  for (i; i < 24; i++) {
    // 分别表示个人编号，姓名，性别，单次连续上网时间，上网总时间，上网次数，标记类型
    let temp = {
      identityId: Random.integer(1, 100),
      name: Random.cname(),
      sex: Random.integer(0, 1),
      age: Random.integer(10, 80),
      onceTime: Random.float(1, 100, 0, 1),
      allTime: Random.float(20, 1000, 0, 1),
      frequency: Random.integer(2, 100)
    }
    value.push(temp)
  }
  // console.log('成功'+data[0].value)
  return value
}
)

// 筛选MyTable个人信息
//  this.$http.post('/tableData',
// { ID: wid,             网吧id
//   ageS:age[0],         年龄范围，start
//   ageE:age[1],         年龄范围，end
//   onceTime:once,       >=单次上网时长
//   allTime:all})        >=上网
Mock.mock('/tableChangeData', /post|get/i, function (options) {
  console.log(options)
  let value = []
  let i = 0
  for (i; i < 6; i++) {
    // 分别表示时间段，正常人数，未成年人数，可疑人员人数
    let temp = {
      identityId: Random.integer(1, 100),
      name: Random.cname(),
      sex: Random.integer(0, 1),
      age: Random.integer(10, 80),
      onceTime: Random.float(1, 20, 0, 1),
      allTime: Random.float(20, 1000, 0, 1),
      frequency: Random.integer(2, 100)
    }
    value.push(temp)
  }
  // console.log('成功'+data[0].value)
  return value
}
)

// calendar
// 当点击MyTable中一项时，通过(网吧Wid：wid,个人Pid:pid)获取上网记录
// [[date,timeslot],...] 日期，上网时长
// this.$http.post('/calendarData',{Pid: pid,Wid:wid} )
Mock.mock('/calendarData', /post|get/i, function (options) {
  console.log(options)
  let value = []
  let i = 0
  for (i; i < Random.integer(5, 40); i++) {
    // 分别表示日期，上网时长
    let temp = []
    let day = Random.date('dd')
    let month = Random.integer(10, 12)
    let date = '2016-' + month + '-' + day
    temp.push(date)
    temp.push(Random.float(0, 24, 0, 1))
    value.push(temp)
    console.log(temp)
  }

  // console.log('成功'+data[0].value)
  return value
}
)



