<template>
<div class="container">
  <div id="ring"  style="width:100%;height:100%"></div>
</div>
</template>

<script>
  import axios from 'axios'
  import bus from '../../assets/eventBus.js'
  export default {
    name: 'ring',
    data(){
            return{
                name:'',
                data:[],
                wid:''
            }
          },
        mounted(){
            this.drawRing(this.name,this.wid)
            let self=this
            bus.$on('clickMap',function(name,wid){
                self.drawRing(name,wid)
            })   
        },
        methods: {
            
            drawRing(name,wid){
                // 基于准备好的dom，初始化echarts实例
                   let self=this
                   self.name=name
                   self.$http.post('/ringData',{ID: wid}).then(function(res){
                    console.log('成功')
                    self.data=res.data
                    // console.log(self.data)
                    let myChart = self.$echarts.init(document.getElementById('ring'))
                      myChart.setOption(
                    
                    {
                      title: {
                          top: 0,
                          text: '年龄分布情况',
                          left: 'center',
                          textStyle: {
                                color: '#fff',
                                fontSize: 16
                            }
                        },
                 tooltip: {
                    trigger: 'item',
                    formatter: "{a} <br/>{b}: {c} ({d}%)"
                  },
                  legend: {
                      orient: 'vertical',
                      x: 'left',
                      data:['<18','18-40','40-60','其它'],
                      textStyle:{
                        color:'#fff',
                        fontSize:'10'
                      },
                      left:'5%',
                      top:'15%'
                   },
                  series: [
                      {
                          name:self.name,
                          type:'pie',
                          radius: ['35%', '50%'],
                          avoidLabelOverlap: false,
                          selectedOffset: 'single',
                          center:['60%','50%'],
                          label: {
                              fontSize:8,
                              normal: {
                                  show: false,
                                  position: 'center'
                              },
                              emphasis: {
                                  show: true,
                                  textStyle: {
                                      fontSize: '15',
                                      fontWeight: 'bold'
                                  }
                              }
                          },
                          labelLine: {
                              normal: {
                                  show: false
                              }
                          },
                          data:self.data
                      }
                  ]
                });
                })
              
            }
        },
  
  }
  
</script>
<style>
 .container{
   padding-top: 2%;
   display: flex;
   flex-direction: column;
   justify-content: flex-start;
   align-items:center;
   width: 300px;
   height: 220px;
 }
</style>



