<template>
<div id="container">
  <div id="scatter" style="width:400px;height:400px"></div>
</div>
</template>

<script>
  import bus from '../../assets/eventBus.js'
  export default {
    name: 'scatter',
    data(){
        return{
            data:[],
            itemStyle: {
                normal: {
                    opacity: 0.8,
                    shadowBlur: 10,
                    shadowOffsetX: 0,
                    shadowOffsetY: 0,
                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
          },
          name:'',
          wid:''
    }          
    },
    mounted(){
            this.drawScatter(this.name,this.wid);
            let self=this
            bus.$on('clickMap',function(name,wid){
                self.drawScatter(name,wid)
            })          
        },
    methods: {
            drawScatter(name,wid){
                 let self=this
                 console.log(wid)
                 this.$http.post('/scatterData',
                        {ID: wid}                             
                 ).then((res)=>{
                    let myChart = self.$echarts.init(document.getElementById('scatter'))
                    // 绘制图表
                    let dataX=[]
                    let dataY1=[]
                    let dataY2=[]
                    let dataY3=[]
                    let dataY4=[]
                    for(let x=0;x<24;x++){
                        dataX.push(res.data[x][0])
                        dataY1.push(res.data[x][1])
                        dataY2.push(res.data[x][2])
                        dataY3.push(res.data[x][3])
                        dataY4.push(res.data[x][4])
                    }
                   
                   myChart.setOption({   
                            title: {
                                top: 0,
                                text:name,
                                left: 'center',
                                textStyle: {
                                        color: '#fff',
                                        fontSize: 16
                                    }
                            },             
                            color: [
                                '#c23531', '#2f4554', '#61a0a8','#d48265'
                            ],
                             legend: {
                                top:24,
                                data: ['<18', '18-40', '40-60','其它'],
                                textStyle: {
                                  color: '#fff'
                                }
                            },
                            tooltip: {
                                trigger: 'axis',
                                axisPointer: {
                                    type: 'cross',
                                    label: {
                                        backgroundColor: '#6a7985'
                                    }
                                }
                            },
                             grid: {
                                left: '3%',
                                right: '11%',
                                bottom: '2%',
                                top:'19%',
                                containLabel: true
                                
                            },
                            xAxis: {
                                type: 'category',
                                name:'时间',
                                axisTick: {show: false},
                                boundaryGap : false,
                                data:dataX,
                                axisLabel:{
                                    color:'#fff'
                                },
                                axisLine:{
                                    lineStyle:{
                                        color:'#fff'
                                    }
                                }
                            },
                            yAxis: {
                                type: 'value',
                                name:'人数',
                                axisLabel:{
                                    color:'#fff'
                                },
                                axisLine:{
                                    lineStyle:{
                                        color:'#fff'
                                    }
                                }
                            },
                            // visualMap: [
                            //     {
                            //         left: 'right',
                            //         top: '10%',
                            //         dimension: 2,
                            //         min: 300,
                            //         max: 500,
                            //         itemWidth: 15,
                            //         itemHeight: 70,
                            //         calculable: true,
                            //         precision: 0.1,
                            //         text: ['圆形大小：接纳人数'],
                            //         textGap: 18,
                            //         textStyle: {
                            //             color: '#fff',
                            //             fontSize: 12
                            //         },
                            //         inRange: {
                            //             symbolSize: [4, 30],
                            //             color: ['#c23531']
                            //         },
                            //         outOfRange: {
                            //             symbolSize: [4, 15],
                            //             color: ['rgba(255,255,255,.2)']
                            //         },
                            //         controller: {
                            //             inRange: {
                            //                 color: ['#c23531']
                            //             },
                            //             outOfRange: {
                            //                 color: ['#444']
                            //             }
                            //         }
                            //     },
                            //     {
                            //         left: 'right',
                            //         bottom: '15%',
                            //         dimension: 6,
                            //         min: 0,
                            //         max: 50,
                            //         itemHeight: 70,
                            //         itemWidth: 15,
                            //         calculable: true,
                            //         precision: 0.1,
                            //         text: ['明暗：可疑人员人数'],
                            //         textGap: 20,
                            //         textStyle: {
                            //             color: '#fff'
                            //         },
                            //         inRange: {
                            //             colorLightness: [1, 0.5]
                            //         },
                            //         outOfRange: {
                            //             color: ['rgba(255,255,255,.2)']
                            //         },
                            //         controller: {
                            //             inRange: {
                            //                 color: ['#c23531']
                            //             },
                            //             outOfRange: {
                            //                 color: ['#444']
                            //             }
                            //         }
                            //     }
                            // ],
                            series: [
                                {
                                    name: '<18',
                                    type: 'line',
                                    areaStyle: {},
                                    stack: '总量',
                                    data: dataY1
                                },
                                {
                                    name: '18-40',
                                    type: 'line',
                                    areaStyle: {},
                                    stack: '总量',
                                    data: dataY2
                                },
                                {
                                    name: '40-60',
                                    type: 'line',
                                    areaStyle: {},
                                    stack: '总量',
                                    data: dataY3
                                },
                                {
                                    name: '其它',
                                    type: 'line',
                                    areaStyle: {},
                                    stack: '总量',
                                    data: dataY4
                                }
                            ]
                    })
              })
     // 基于准备好的dom，初始化echarts实例
          console.log(this.wid)
        }
     }
  }
  
</script>
<style>
 #container{
   display: flex;
   flex-direction: column;
   justify-content: flex-start;
   align-items:center;
   width: 300px;
   height: 400px;
 }
</style>



